import { Day } from "../types";
import { loadImage } from "../helper";

import qrCode from './qr-code.png';

import bNgqbpqOShoQqXtmncgWNLN from './frames/frame_0000.jpg';
import UipcnGQXTkwPXOgLAZMCDo0 from './frames/frame_0001.jpg';
import fXnMPlxtoyTUncCMfuXsIxy from './frames/frame_0002.jpg';
import cWLCGgySQbWThOAaPnlRuhM from './frames/frame_0003.jpg';
import dBfWiqWcyLzYObtFQqrDkMH from './frames/frame_0004.jpg';
import eYSaeAtZwJwkPqVGGzTdLFz from './frames/frame_0005.jpg';
import dabqteZdpmNATyrWQfqGOnf from './frames/frame_0006.jpg';
import cyRkIFEbUnjpJlSqQzuJtSo from './frames/frame_0007.jpg';
import dxfMkqgYPVVrHbZqyzAkRGa from './frames/frame_0008.jpg';
import YDdWuNynnFTmtJrTEWoYNn0 from './frames/frame_0009.jpg';
import fWssFXxQFQrItlzGcnvcCZV from './frames/frame_0010.jpg';
import efdjgNVnLIpxwNxXrbWirXG from './frames/frame_0011.jpg';
import YnoKDrBNugtXMwxLuDSYTX0 from './frames/frame_0012.jpg';
import eBdiNfZjlCmEjhkTWDZwbKG from './frames/frame_0013.jpg';
import epRIssnxmNAhQwTScKhiPvS from './frames/frame_0014.jpg';

import preval from 'preval.macro';

export default class Day02 extends Day {
  public static __hash = "aiDjqteQNhVBRLiHceCSRIO";
  public static __name = "Day02";

  public static __qrCode = qrCode;

  public static framesPromises = [
    loadImage(bNgqbpqOShoQqXtmncgWNLN),
    loadImage(UipcnGQXTkwPXOgLAZMCDo0),
    loadImage(fXnMPlxtoyTUncCMfuXsIxy),
    loadImage(cWLCGgySQbWThOAaPnlRuhM),
    loadImage(dBfWiqWcyLzYObtFQqrDkMH),
    loadImage(eYSaeAtZwJwkPqVGGzTdLFz),
    loadImage(dabqteZdpmNATyrWQfqGOnf),
    loadImage(cyRkIFEbUnjpJlSqQzuJtSo),
    loadImage(dxfMkqgYPVVrHbZqyzAkRGa),
    loadImage(YDdWuNynnFTmtJrTEWoYNn0),
    loadImage(fWssFXxQFQrItlzGcnvcCZV),
    loadImage(efdjgNVnLIpxwNxXrbWirXG),
    loadImage(YnoKDrBNugtXMwxLuDSYTX0),
    loadImage(eBdiNfZjlCmEjhkTWDZwbKG),
    loadImage(epRIssnxmNAhQwTScKhiPvS),
  ];

  public static __content = async () => preval`
    const marked = require('marked');
    const twemoji = require('twemoji');
    const path = require('path');
    const fs = require('fs');

    const markdownPath = path.join(__dirname, 'content.md');
    const markdown = fs.readFileSync(markdownPath, 'utf-8')

    const _html = marked(markdown);
    const html = twemoji.parse(_html);

    module.exports = html;
  `;

  private static frames: HTMLImageElement[];
  private static currentFrame: number = 0;

  private static fps = 1000 / 20;
  private static last = 0;

  public static async init() {
    if (Day02.frames === undefined) {
      await Day02.loadFrames();
    }
  }

  private static async loadFrames() {
    Day02.frames = await Promise.all(Day02.framesPromises);
  }

  public static getFrame() {    
    const now = Date.now();
    const elapsed = now - Day02.last;

    if (elapsed >= Day02.fps) {
      Day02.last = now;
      Day02.currentFrame = (Day02.currentFrame + 1) % Day02.frames.length;
    }

    return Day02.frames[Day02.currentFrame];
  }
}
